@use "../../styles/variables" as *;

.update__housewife-background {
 
  position: fixed; /* Stay in place */
  z-index: 1; /* Sit on top */
  left: 0;
  top: 0;
  width: 100%; 
  height: 100%; 
  overflow: auto; 
  background-color: rgb(0,0,0); 
  background-color: rgba(0,0,0,0.4); 

  .update__housewife {
    background-color: $color-primary;
    margin: 15% auto; 
    padding: 20px;
    border: 1px solid $color-primary;
    width: 20rem; 
    border-radius: 10px;
    box-shadow: 0px 2px 5px rgba(0,0,0,.70);
  }
  .update__button {
    background-color: white;
    color: $color-green;
    font-size: 15px;
    font-weight: bold;
    font-family: $font-primary;
    border: 1px solid $color-green;
    margin-top: 1rem;
    border-radius: 5px;
    padding: 5px;
  }
  .update__button:hover,
.update__button:focus {
  color: black;
  text-decoration: none;
  cursor: pointer;
}

input {
  width: 20rem;
  height: 2rem;
  font-family: $font-primary;
  margin: 1px;
  background-color: rgb(228, 224, 224);
  border: 1px solid rgb(202, 165, 165);
  border-radius: 10px;
}

}
